.List {
  right: 0;
  background-color: rgba(255, 255, 255, 0.9);
}

.list-header {
  height: 130px;
  border-bottom: 0.5px solid rgba(151, 151, 151, 0.9);
}

.logo-image {
  margin-left: 2px;
}

.filter-title {
  font-family: 'Montserrat', sans-serif;
  font-size: 0.95rem;
  font-weight: 500;

  white-space: nowrap;
  padding: 0 5px 0 5px;
  cursor: pointer;
  /*font-weight: 500;*/
  margin: 0.2em;
  border-radius: 0.3em;
  line-height: 27px;
  height: 27px;
}

.list-container {
  overflow: scroll;
  height: 100%;
}

.react-search-field {
  width: 320px;
  border: none !important;
}

.react-search-field-button {
  border: 1px solid rgb(221, 221, 221) !important;
}

.react-search-field-button>svg {
  margin-top: -5px;
}

.react-search-field-input {
  font-family: 'Montserrat', sans-serif;
  font-size: 17px !important;
  border: 1px solid rgb(221, 221, 221);
  border-top-left-radius: 0.3em;
  border-bottom-left-radius: 0.3em;
  box-shadow: inset 1px 1px 3px 0px #D5D6D6;
}

.titles {
  margin: 0;
  margin-left: -3px;
}

.ReactVirtualized__List {
    outline: none;
}

.blur-bottom-corner {
    position: absolute;
    /*bottom: 29px;*/
}

.to-all-branches {
  color: rgba(204, 76, 64, 0.8);
  font-family: 'Montserrat', sans-serif;
  font-size: 0.8rem;
  font-weight: bold;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-left: 2px;
  margin-top: 5px;
}

/*.to-all-branches:hover {*/
/*  cursor: pointer;*/
/*  text-decoration: underline;*/
/*}*/

.ReactVirtualized__List::-webkit-scrollbar {
  background-color: Transparent;
  width: 5px;
}
.ReactVirtualized__List::-webkit-scrollbar-thumb {
  background-color: rgb(173, 174, 177);
  border-radius: 1rem;
}

li {
  list-style-type: none; /* Убираем маркеры */
}

ul {
  margin-left: 0; /* Отступ слева в браузере IE и Opera */
  padding-left: 0; /* Отступ слева в браузере Firefox, Safari, Chrome */
}

.filter-item {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-size: 1.05rem;
  width: 25px;
  margin: 0px 2px;
}

.list-item {
  font-family: 'Roboto', sans-serif;
  font-size: 0.9rem;
  /*font-weight: 400;*/
  /*height: 40px;*/
  border-bottom: 0.5px solid rgba(151, 151, 151, 0.9);
  cursor: pointer;
  white-space: nowrap;
}

.list-item>div {
  display: inline-block;
}

.list-item .name {
  padding: 0;
  margin: 0;
  font-size: 14px;
  white-space: pre-wrap;
  /*white-space: nowrap;*/
  overflow: hidden;
  text-overflow: ellipsis;
}

.list-item:hover {
  background-color: rgba(235, 237, 240, 0.85);
}

.list-item .filter-item {
  font-weight: 600;
  border-radius: 0.25em;
  min-width: 25px;
}

.remain-info {
  display: flex;
  align-items: center;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 15px;
    color: #929394;
    position: absolute;
    bottom: 0;
    background-color: rgba(255,255,255,0.8);
}

.exit {
  cursor: pointer;
}

.exit img {
  transition: 0.2s;
}

.exit img:hover {
  transform: scale(1.2);
}
