.waiter {
  position: absolute;
  z-index: 3;
  background-color: rgba(255, 255, 255, 0.8);
}

.logo {
    position: absolute;
  opacity: .8;
}

.Auth {
  position: absolute;
  z-index: 2;
  background-color: rgba(255, 255, 255, 0.8);
  font-size: 1.1em;
}

.Auth input {
  width: 300px;
  outline: none;
  padding-left: 5px;
  border: 1px solid #D5D6D6;
  border-radius: 0.3em;
  box-shadow: inset 1px 1px 3px 0px #D5D6D6;
}

.waiting {
  cursor: wait;
}

.btn {
  background-color: #6DAD9D !important;
  border-color: #6DAD9D !important;
  line-height: 100%;
  height: 30px;
  width: 80px;
}

.btn:hover {
  cursor: pointer !important;
}

.btn:focus, .btn:active {
  outline: none !important;
  box-shadow: none !important;
}

.warning {
  height: 50px;
  color: #C93638;
}
.cluster {
  width: 30px;
  height: 30px;
  color: #fff;
  margin-left: -50%;
  border-radius: 100%;
  display: -webkit-flex;
  display: flex;
  cursor: pointer;
}

.cluster .title {
  font-size: 16px;
}

.map {
  position: absolute;
  z-index: 0;
}

.Interface {
    position: fixed;
    z-index: 10;
    min-width: 800px;
}

.Interface.frame-closed {
    min-width: unset;
    right: 0;
}
.Frame iframe {
    background-color: white;
    border: 1px solid rgb(221, 221, 221);
}

.iframe-container {
    height: 100%;
    width: 100%;
    position: relative;
}

.close-button {
    cursor: pointer;
    position: absolute;
    top: -0.1rem;
    right: -1.7rem;
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 1;
    color: #000;
    text-shadow: 0 1px 0 #fff;
    opacity: .5;
    transition: 0.2s;
}

.close-button:hover {
    opacity: .9;
}
.List {
  right: 0;
  background-color: rgba(255, 255, 255, 0.9);
}

.list-header {
  height: 130px;
  border-bottom: 0.5px solid rgba(151, 151, 151, 0.9);
}

.logo-image {
  margin-left: 2px;
}

.filter-title {
  font-family: 'Montserrat', sans-serif;
  font-size: 0.95rem;
  font-weight: 500;

  white-space: nowrap;
  padding: 0 5px 0 5px;
  cursor: pointer;
  /*font-weight: 500;*/
  margin: 0.2em;
  border-radius: 0.3em;
  line-height: 27px;
  height: 27px;
}

.list-container {
  overflow: scroll;
  height: 100%;
}

.react-search-field {
  width: 320px;
  border: none !important;
}

.react-search-field-button {
  border: 1px solid rgb(221, 221, 221) !important;
}

.react-search-field-button>svg {
  margin-top: -5px;
}

.react-search-field-input {
  font-family: 'Montserrat', sans-serif;
  font-size: 17px !important;
  border: 1px solid rgb(221, 221, 221);
  border-top-left-radius: 0.3em;
  border-bottom-left-radius: 0.3em;
  box-shadow: inset 1px 1px 3px 0px #D5D6D6;
}

.titles {
  margin: 0;
  margin-left: -3px;
}

.ReactVirtualized__List {
    outline: none;
}

.blur-bottom-corner {
    position: absolute;
    /*bottom: 29px;*/
}

.to-all-branches {
  color: rgba(204, 76, 64, 0.8);
  font-family: 'Montserrat', sans-serif;
  font-size: 0.8rem;
  font-weight: bold;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  margin-left: 2px;
  margin-top: 5px;
}

