/*  cursor: pointer;*/
/*  text-decoration: underline;*/
/*}*/

.ReactVirtualized__List::-webkit-scrollbar {
  background-color: Transparent;
  width: 5px;
}
.ReactVirtualized__List::-webkit-scrollbar-thumb {
  background-color: rgb(173, 174, 177);
  border-radius: 1rem;
}

li {
  list-style-type: none; /* Убираем маркеры */
}

ul {
  margin-left: 0; /* Отступ слева в браузере IE и Opera */
  padding-left: 0; /* Отступ слева в браузере Firefox, Safari, Chrome */
}

.filter-item {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-size: 1.05rem;
  width: 25px;
  margin: 0px 2px;
}

.list-item {
  font-family: 'Roboto', sans-serif;
  font-size: 0.9rem;
  /*font-weight: 400;*/
  /*height: 40px;*/
  border-bottom: 0.5px solid rgba(151, 151, 151, 0.9);
  cursor: pointer;
  white-space: nowrap;
}

.list-item>div {
  display: inline-block;
}

.list-item .name {
  padding: 0;
  margin: 0;
  font-size: 14px;
  white-space: pre-wrap;
  /*white-space: nowrap;*/
  overflow: hidden;
  text-overflow: ellipsis;
}

.list-item:hover {
  background-color: rgba(235, 237, 240, 0.85);
}

.list-item .filter-item {
  font-weight: 600;
  border-radius: 0.25em;
  min-width: 25px;
}

.remain-info {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 15px;
    color: #929394;
    position: absolute;
    bottom: 0;
    background-color: rgba(255,255,255,0.8);
}

.exit {
  cursor: pointer;
}

.exit img {
  transition: 0.2s;
}

.exit img:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

